import { defineComponent, ref } from 'vue';
import loginAccountVue from './login-account.vue';
import loginPhoneVue from './login-phone.vue';
export default defineComponent({
  components: {
    loginAccountVue,
    loginPhoneVue
  },

  setup() {
    // 1.定义属性
    const iskeepPassword = ref(false); // 拿到组件对象

    const accoutRef = ref();
    const phoneRef = ref(); // 两个登陆方式的切换逻辑判断

    const currentTab = ref('account'); // 2.定义方法

    const UsersLoginClick = () => {
      // console.log('立即登录', accoutRef.value)
      if (currentTab.value == 'account') {
        accoutRef.value?.LoadingAction(iskeepPassword.value);
      } else {
        console.log('phoneRef调用loginAction');
      }
    };

    return {
      iskeepPassword,
      UsersLoginClick,
      accoutRef,
      currentTab,
      phoneRef
    };
  }

});